@use "global" as *;
.tooltip {
  background: $color-bg-base;
  width: 140px;
  height: 30px;
  padding: 2px 5px 5px 8px;
  border-radius: 6px;
  position: relative;

  &::after {
      content: "";
      position: absolute;
      top: 100%; //親要素の高さの下側に貼り付ける
      left: 50%; //親要素左端からの距離
      transform: translateX(-50%);
      //三角の指定//
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 8px 0 8px;
      border-color: $color-bg-base transparent transparent transparent;    
  } 

  @include mq('lg') {
    display: none;
  }
}

.tooltip__text-wrap {
  display: flex;
  align-items: baseline;
}


.tooltip__text_md,
.tooltip__text_lg,
.tooltip__text_sm, 
.tooltip__text_xs, 
.tooltip__text_xxs {
  color: $color-main;
  line-height: 1;
}

.tooltip__text_xs {
  font-size: 10px;
}

.tooltip__text_md {
  font-size: 12px;
}

.tooltip__text_lg {
  font-size: 20px;
  font-weight: $font-bold;
}

span.tooltip__text_sm {
  font-size: 11px;
  font-weight: $font-bold;
}

.tooltip__text_xxs {
  font-size: 10px !important;
  display: block;
  transform: scale(0.6);
  transform-origin: 0 0;
  position: absolute;
  top: 58%;
  right: -21px;
}
